// =============================================================================
// WPFS Variables
// =============================================================================

@import 'globals/variables';
@import 'globals/functions';

[class*='#{$wpfs}-'] {
  --font-family: #{$system-font};
  --primary-color: #{color('gun-metal')};
  --secondary-color: #{color('navy-blue')};
  --error-color: #{color('mordant-red')};
  --success-color: #{color('green')};

  --default-border-radius: #{$default-border-radius};

  --form-bg-color: #fff;

  --form-base-color: #{color('silver')};
  --form-secondary-color: #{color('x11-gray')};

  --form-control-bg-color: #fff;
  --form-control-border-color: #{color('silver')};
  --form-control-placeholder-color: #{color('silver')};
  --form-control-hover-border-color: #{color('silver')};

  --form-check-input-disabled-bg-color: #{rgba(color('x11-gray'), .3)};

  --form-message-font-color: #fff;

  --ui-widget-content-bg-color: #fff;

  --ui-state-active-bg-color: #{color('alice')};
  --ui-state-active-font-color: #fff;

  --ui-state-selected-bg-color: #{color('navy-blue')};
  --ui-state-selected-font-color: #fff;

  --ui-info-tooltip-font-color: #fff;

  --ui-summary-tooltip-bg-color: #fff;
  --ui-summary-tooltip-border-color: #{color('x11-gray')};

  --link-font-color: #{color('navy-blue')};
  --link-hover-font-color: #{lighten(color('navy-blue'), 5%)};
  --link-active-font-color: #{color('navy-blue')};
  --link-disabled-font-color: #{color('x11-gray')};
}

.#{$wpfs}-form {
  --form-thank-you-message-font-color: #{color('gun-metal')};
}

.#{$wpfs}-btn {
  --primary-bg-color: #{color('navy-blue')};
  --primary-font-color: #fff;
  --primary-hover-focus-bg-color: #{lighten(color('navy-blue'), 5%)};
  --primary-active-bg-color: #{color('navy-blue')};
  --primary-disabled-bg-color: #{color('x11-gray')};
  --primary-disabled-font-color: #{rgba(#fff, .6)};

  --danger-bg-color: #{color('mordant-red')};
  --danger-font-color: #fff;
  --danger-hover-focus-bg-color: #{lighten(color('mordant-red'), 5%)};
  --danger-active-bg-color: #{color('mordant-red')};
  --danger-disabled-bg-color: #{color('x11-gray')};
  --danger-disabled-font-color: #{rgba(#fff, .6)};

  --outline-primary-bg-color: #fff;
  --outline-primary-hover-focus-border-color: #{color('navy-blue')};
  --outline-primary-border-color: #{color('x11-gray')};
  --outline-primary-disabled-border-color: #{color('x11-gray')};
  --outline-primary-disabled-font-color: #{rgba(#fff, .6)};
  --outline-primary-selected-font-color: #fff;
}
